




























import Vue from "vue";
import { mapActions, mapState, mapGetters, mapMutations } from "vuex";
export default Vue.extend({
  props: {},
  components: {},
  data() {
    return {
      isShow: true
    };
  },
  computed: {
    ...mapGetters({
      sortArr:"car/sortArr",
      yearType:"car/yearType"
    })
  },
  methods: {
     ...mapMutations({
      getTypeId:"picture/getTypeId"
    }),
    toPicture(type:object){
      this.getTypeId(type)
      this.$router.push({
        name:"picture",
      })
    }
  },
  created() {},
  mounted() {
    console.log("arr",this.yearType)
  }
});
